/* ––––––––––––––––––––––––––––––––––––––––––––––––––
PAGINATION
–––––––––––––––––––––––––––––––––––––––––––––––––– */

.pagination {
  margin: $baseline 0;
  list-style: none;

  li {
    @include transitions;
    display: inline-block;

    &:last-child {
      margin-right: 0;
    }

    &:hover {

      a {
        background: $pagination-hover-color;
        color: $base-font-color;
      }

    }

    a {
      display: inline-block;
      padding: .4em .8em;
    }

    &.active {

      a {
        background-color: $pagination-active-background;
        color: $white;
      }

    }

  }

  &.center {
    width: 100%;
    text-align: center;
  }

  &.right {
    width: 100%;
    text-align: right;
  }

}